"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { useMutation, useQueryClient } from "@tanstack/react-query";
import { useRouter } from "next/navigation";
import { Button } from "@bitflow/ui/components/button";
import { Input } from "@bitflow/ui/components/input";
import { Label } from "@bitflow/ui/components/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@bitflow/ui/components/select";
import { Textarea } from "@bitflow/ui/components/textarea";
import { ArrowLeft } from "lucide-react";
import Link from "next/link";

// Mock data
const mockCourses = [
  { id: "1", name: "Computer Science" },
  { id: "2", name: "Mechanical Engineering" },
  { id: "3", name: "Business Administration" },
];

const mockFaculty = [
  { id: "1", name: "Dr. Rajesh Kumar" },
  { id: "2", name: "Prof. Priya Sharma" },
  { id: "3", name: "Dr. Amit Patel" },
  { id: "4", name: "Dr. Sarah Johnson" },
  { id: "5", name: "Prof. Anjali Verma" },
];

// Validation schema
const subjectSchema = z.object({
  subjectCode: z
    .string()
    .min(1, "Subject code is required")
    .regex(/^[A-Z]{2,4}[0-9]{3}-(TH|PR|LB)$/, "Format: CS101-TH, ME201-PR, or BUS301-LB"),
  subjectName: z
    .string()
    .min(3, "Subject name must be at least 3 characters")
    .max(100, "Subject name must not exceed 100 characters"),
  courseId: z.string().min(1, "Course is required"),
  type: z.enum(["theory", "practical"], {
    required_error: "Subject type is required",
  }),
  credits: z
    .number()
    .min(1, "Credits must be at least 1")
    .max(6, "Credits must not exceed 6"),
  hoursPerWeek: z
    .number()
    .min(1, "Hours per week must be at least 1")
    .max(20, "Hours per week must not exceed 20"),
  semester: z
    .number()
    .min(1, "Semester must be at least 1")
    .max(8, "Semester must not exceed 8"),
  facultyId: z.string().min(1, "Faculty is required"),
  prerequisites: z.string().optional(),
  description: z.string().optional(),
  status: z.enum(["active", "inactive"], {
    required_error: "Status is required",
  }),
});

type SubjectFormData = z.infer<typeof subjectSchema>;

export default function CreateSubjectPage() {
  const router = useRouter();
  const queryClient = useQueryClient();

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    watch,
  } = useForm<SubjectFormData>({
    resolver: zodResolver(subjectSchema),
    defaultValues: {
      status: "active",
      type: "theory",
    },
  });

  const createMutation = useMutation({
    mutationFn: async (data: SubjectFormData) => {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1000));
      console.log("Creating subject:", data);
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["subjects"] });
      router.push("/subjects");
    },
  });

  const onSubmit = (data: SubjectFormData) => {
    createMutation.mutate(data);
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center gap-4">
        <Link href="/subjects">
          <Button variant="ghost" size="sm">
            <ArrowLeft className="h-4 w-4" />
          </Button>
        </Link>
        <div>
          <h1 className="text-3xl font-bold">Add New Subject</h1>
          <p className="text-muted-foreground">
            Create a new subject for the curriculum
          </p>
        </div>
      </div>

      {/* Form */}
      <form onSubmit={handleSubmit(onSubmit)} className="space-y-8">
        {/* Basic Information */}
        <div className="rounded-lg border p-6 space-y-4">
          <h2 className="text-xl font-semibold">Basic Information</h2>
          <div className="grid gap-4 md:grid-cols-2">
            <div className="space-y-2">
              <Label htmlFor="subjectCode">
                Subject Code <span className="text-red-500">*</span>
              </Label>
              <Input
                id="subjectCode"
                placeholder="CS101-TH, ME201-PR"
                {...register("subjectCode")}
                className={errors.subjectCode ? "border-red-500" : ""}
              />
              {errors.subjectCode && (
                <p className="text-sm text-red-500">
                  {errors.subjectCode.message}
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="subjectName">
                Subject Name <span className="text-red-500">*</span>
              </Label>
              <Input
                id="subjectName"
                placeholder="Introduction to Programming"
                {...register("subjectName")}
                className={errors.subjectName ? "border-red-500" : ""}
              />
              {errors.subjectName && (
                <p className="text-sm text-red-500">
                  {errors.subjectName.message}
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="courseId">
                Course <span className="text-red-500">*</span>
              </Label>
              <Select
                onValueChange={(value) => setValue("courseId", value)}
                defaultValue={watch("courseId")}
              >
                <SelectTrigger className="bg-white text-black">
                  <SelectValue placeholder="Select course" />
                </SelectTrigger>
                <SelectContent className="bg-white">
                  {mockCourses.map((course) => (
                    <SelectItem key={course.id} value={course.id} className="text-black">
                      {course.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.courseId && (
                <p className="text-sm text-red-500">{errors.courseId.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="type">
                Subject Type <span className="text-red-500">*</span>
              </Label>
              <Select
                onValueChange={(value) => setValue("type", value as "theory" | "practical")}
                defaultValue={watch("type")}
              >
                <SelectTrigger className="bg-white text-black">
                  <SelectValue placeholder="Select type" />
                </SelectTrigger>
                <SelectContent className="bg-white">
                  <SelectItem value="theory" className="text-black">Theory</SelectItem>
                  <SelectItem value="practical" className="text-black">Practical</SelectItem>
                </SelectContent>
              </Select>
              {errors.type && (
                <p className="text-sm text-red-500">{errors.type.message}</p>
              )}
            </div>
          </div>
        </div>

        {/* Academic Details */}
        <div className="rounded-lg border p-6 space-y-4">
          <h2 className="text-xl font-semibold">Academic Details</h2>
          <div className="grid gap-4 md:grid-cols-3">
            <div className="space-y-2">
              <Label htmlFor="credits">
                Credits <span className="text-red-500">*</span>
              </Label>
              <Input
                id="credits"
                type="number"
                min="1"
                max="6"
                placeholder="4"
                {...register("credits", { valueAsNumber: true })}
                className={errors.credits ? "border-red-500" : ""}
              />
              {errors.credits && (
                <p className="text-sm text-red-500">{errors.credits.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="hoursPerWeek">
                Hours Per Week <span className="text-red-500">*</span>
              </Label>
              <Input
                id="hoursPerWeek"
                type="number"
                min="1"
                max="20"
                placeholder="5"
                {...register("hoursPerWeek", { valueAsNumber: true })}
                className={errors.hoursPerWeek ? "border-red-500" : ""}
              />
              {errors.hoursPerWeek && (
                <p className="text-sm text-red-500">
                  {errors.hoursPerWeek.message}
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="semester">
                Semester <span className="text-red-500">*</span>
              </Label>
              <Select
                onValueChange={(value) => setValue("semester", parseInt(value))}
                defaultValue={watch("semester")?.toString()}
              >
                <SelectTrigger className="bg-white text-black">
                  <SelectValue placeholder="Select semester" />
                </SelectTrigger>
                <SelectContent className="bg-white">
                  {[1, 2, 3, 4, 5, 6, 7, 8].map((sem) => (
                    <SelectItem key={sem} value={sem.toString()} className="text-black">
                      Semester {sem}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.semester && (
                <p className="text-sm text-red-500">{errors.semester.message}</p>
              )}
            </div>

            <div className="space-y-2 md:col-span-3">
              <Label htmlFor="facultyId">
                Assigned Faculty <span className="text-red-500">*</span>
              </Label>
              <Select
                onValueChange={(value) => setValue("facultyId", value)}
                defaultValue={watch("facultyId")}
              >
                <SelectTrigger className="bg-white text-black">
                  <SelectValue placeholder="Select faculty" />
                </SelectTrigger>
                <SelectContent className="bg-white">
                  {mockFaculty.map((faculty) => (
                    <SelectItem key={faculty.id} value={faculty.id} className="text-black">
                      {faculty.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.facultyId && (
                <p className="text-sm text-red-500">{errors.facultyId.message}</p>
              )}
            </div>
          </div>
        </div>

        {/* Additional Information */}
        <div className="rounded-lg border p-6 space-y-4">
          <h2 className="text-xl font-semibold">Additional Information</h2>
          <div className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="prerequisites">Prerequisites</Label>
              <Input
                id="prerequisites"
                placeholder="CS100, MATH101 (comma separated)"
                {...register("prerequisites")}
              />
              <p className="text-sm text-muted-foreground">
                Enter prerequisite subject codes separated by commas
              </p>
            </div>

            <div className="space-y-2">
              <Label htmlFor="description">Description</Label>
              <Textarea
                id="description"
                placeholder="Brief description of the subject..."
                rows={4}
                {...register("description")}
                className="bg-white text-black"
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="status">
                Status <span className="text-red-500">*</span>
              </Label>
              <Select
                onValueChange={(value) => setValue("status", value as "active" | "inactive")}
                defaultValue={watch("status")}
              >
                <SelectTrigger className="bg-white text-black">
                  <SelectValue placeholder="Select status" />
                </SelectTrigger>
                <SelectContent className="bg-white">
                  <SelectItem value="active" className="text-black">Active</SelectItem>
                  <SelectItem value="inactive" className="text-black">Inactive</SelectItem>
                </SelectContent>
              </Select>
              {errors.status && (
                <p className="text-sm text-red-500">{errors.status.message}</p>
              )}
            </div>
          </div>
        </div>

        {/* Actions */}
        <div className="flex justify-end gap-4">
          <Link href="/subjects">
            <Button type="button" variant="outline">
              Cancel
            </Button>
          </Link>
          <Button type="submit" disabled={createMutation.isPending}>
            {createMutation.isPending ? "Creating..." : "Create Subject"}
          </Button>
        </div>
      </form>
    </div>
  );
}
